import { Injectable, assertPlatform } from '@angular/core';
import { HttpClient } from '@angular/common/http';

// Environment
import { PublicMethods } from '../../_public/-public-methods'
import { Observable } from 'rxjs';
import {Pay} from '../_models/payBill.model'

const _url = PublicMethods.createAdminURL


@Injectable({
    providedIn: 'root'
})


//getbilldetails
export class BillingService {
    constructor(private http: HttpClient) { }
    getBilling(start_date,end_date,club_key){
        var parent_keys = (JSON.parse(localStorage.getItem('user')))['lastloginaccount'];
        var public_keys = (JSON.parse(localStorage.getItem('user')))['pub_key'];
        let BILLING_URL=_url('billing','getclubbill','',parent_keys,public_keys,1,30);
        if(start_date!='') BILLING_URL += '&start_date='+start_date;
        if(end_date!='') BILLING_URL += '&end_date='+end_date;
        if(club_key!='') BILLING_URL += '&club_key='+club_key;
        return this.http.get<any>(BILLING_URL)
    }
    getBillingDetails(start_date,end_date,club_key){
        var parent_keys = (JSON.parse(localStorage.getItem('user')))['lastloginaccount'];
        var public_keys = (JSON.parse(localStorage.getItem('user')))['pub_key'];
        let BILLING_URL=_url('billing','getbilldetails','',parent_keys,public_keys,1,30);
        if(start_date!='') BILLING_URL += '&start_date='+start_date;
        if(end_date!='') BILLING_URL += '&end_date='+end_date;
        if(club_key!='') BILLING_URL += '&club_key='+club_key;
        return this.http.get<any>(BILLING_URL)
    }
    getTransaction(start_date,end_date,club_key){
        var parent_keys = (JSON.parse(localStorage.getItem('user')))['lastloginaccount'];
        var public_keys = (JSON.parse(localStorage.getItem('user')))['pub_key'];
        let BILLING_URL=_url('billing','getclubtransactiondetails','',parent_keys,public_keys,1,30);
        if(start_date!='') BILLING_URL += '&start_date='+start_date;
        if(end_date!='') BILLING_URL += '&end_date='+end_date;
        if(club_key!='') BILLING_URL += '&club_key='+club_key;
        return this.http.get<any>(BILLING_URL)
    }
    createPayment(bill_Key,method,date): Observable<any>{
        var public_keys = (JSON.parse(localStorage.getItem('user')))['pub_key'];
        let PAY_URL=_url('billing','payClubBill','','',public_keys,0,0);
        return this.http.post(PAY_URL,{bill_Key,method,date})
    }
    getAllBills(start_date,end_date){
        var parent_keys = (JSON.parse(localStorage.getItem('user')))['lastloginaccount'];
        var public_keys = (JSON.parse(localStorage.getItem('user')))['pub_key'];
        let BILLING_URL=_url('billing','getbill','',parent_keys,public_keys,1,30);
        if(start_date!='') BILLING_URL += '&start_date='+start_date;
        if(end_date!='') BILLING_URL += '&end_date='+end_date;
        return this.http.get<any>(BILLING_URL)
    }
}