import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { Subscription } from 'rxjs';
import { SelectionModel } from '@angular/cdk/collections';
import { LayoutUtilsService, MessageType } from '../../../core/_base/crud';
import { MatPaginator, MatDialog, MatTableDataSource } from '@angular/material';
// Components
import { EditMembershipComponent } from './edit-membership/edit-membership.component';
//models 
import { Membership } from '../../../core/auth/_models/membership.model';
import { MembershipService } from '../../../core/auth/_services/membership.service';
//ngrx
import { Store } from '@ngrx/store';
import { AppState } from '../../../core/reducers';
import { MembershipDeleted } from '../../../core/auth';
@Component({
  selector: 'kt-memberships',
  templateUrl: './memberships.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MembershipsComponent implements OnInit, OnDestroy {
  
  //MatTableDataSource<any>
  displayedColumns = ['select', 'title', 'description', 'fees', 'maxCurrentUser', 'actions'];
  selection = new SelectionModel<Membership>(true, []);
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  membershipsResult :Membership [];
  dataSource;
  baseUrl;


  // Subscriptions
  private subscriptions: Subscription[] = [];
  constructor(
    private MembershipService: MembershipService,
    public dialog: MatDialog,
    private layoutUtilsService: LayoutUtilsService,
    private store: Store<AppState>,
    private changeDetectorRefs: ChangeDetectorRef
  ) {

  }

  ngOnInit() {
    this.getMemberships();
  }

  ngOnDestroy() {
    this.subscriptions.forEach(el => el.unsubscribe());
  }


  getMemberships() {
    this.MembershipService.getMemberships().subscribe((res) => {
      this.membershipsResult = res['data']['result'];
      this.baseUrl=res['data']['Url']
      this.dataSource = new MatTableDataSource<Membership>(this.membershipsResult);
      this.dataSource.paginator = this.paginator;
      this.changeDetectorRefs.detectChanges();
      console.log('this.membershipsResult',this.membershipsResult);
      console.log('this.dataSource',this.dataSource.data[0].maxCurrentUser.allBranches)
    })
  }
  addMembership() {
    const membership = new Membership();
    
   // Set all defaults field
    console.log(membership)
    this.editMembership(membership)
  }
  deleteMembership(membership) {
    let _id = membership._id;
    const _title: string = 'Membership';
    const _description: string = 'Are you sure to permanently delete this Membership?';
    const _waitDesciption: string = 'Membership is deleting...';
    const _deleteMessage = `Membership has been deleted`;
    
      const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
      dialogRef.afterClosed().subscribe(res => {
        if (!res) {
          return;
        } else{
          this.MembershipService.deleteMembership(_id,membership.image).subscribe((res) => {
            this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
          })
          this.getMemberships()
        }
        
        
    
      this.store.dispatch(new MembershipDeleted({ id: membership._id }));
    })
    console.log(membership)
  }
  fetchMemberships() {
    const messages = [];
    this.selection.selected.forEach(elem => {
      messages.push({
        text: ` title:${elem.title},status: ${elem.status} , id:${elem._id} `,
      });
    });
    this.layoutUtilsService.fetchElements(messages);
  }

  editMembership(membership) {
    const _saveMessage = `Membership successfully has been saved.`;
    const _messageType = membership.id ? MessageType.Update : MessageType.Create;
    console.log(membership);

    const dialogRef = this.dialog.open(EditMembershipComponent, { data: { membership ,Url:this.baseUrl } ,});
    dialogRef.afterClosed().subscribe(res => {
      if (!res) {
				return; 
			}

			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType);
			this.getMemberships();
      })
      

  }
  //select
  isAllSelected(): boolean {
    const numSelected = this.selection.selected.length;
    const numRows = this.membershipsResult.length;
    console.log(this.membershipsResult.length)
    return numSelected === numRows;
  }
  masterToggle() {
    if (this.selection.selected.length === this.membershipsResult.length) {
      this.selection.clear();
    } else {
      this.membershipsResult.forEach(row => this.selection.select(row));
    }
  }
  //End select
  checkedpermission(key) {
    if (localStorage.getItem('user')) {

      if (JSON.parse(localStorage.getItem('user'))['allPrivilidge']) {
        if (JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Roles']) {
          if (key == 'del') {
            if (JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Membership']['Edit Membership'])
              return true
          }
          else if (key == 'update') {
            if (JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Membership']['Delete Membership'])
              return true
          }
          else if (key = 'add') {
            if (JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Membership']['Add Membership'])
              return true
          }
        }
      }
    }

    return false
  }
}
