import { Component, OnInit ,Inject} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { PermissionsService } from '../../../../../core/auth/_services/permissions.sevice';
import{UserService} from '../../../../../core/auth/_services/user.service'
import { from } from 'rxjs';
import{User} from '../../../../../core/auth/_models/user.model';
import { Router } from '@angular/router';
@Component({
  selector: 'kt-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.scss']
})
export class AddUserComponent implements OnInit {
  userForm: FormGroup;
  hasFormErrors: boolean = false;
  allPermissions$;
  role;
  errorMessage;
  constructor(private userFB: FormBuilder,
    public dialogRef: MatDialogRef<AddUserComponent>,
    private permission: PermissionsService,
    private UserService:UserService,
    private router:Router
    ) { }

  ngOnInit() {
    this.createForm();
    // this.permission.getPriviliedgesType().subscribe(res => {
    //   this.allPermissions$ = res;
    //   console.log(this.allPermissions$.result)
    // });
    
  }
  createForm() {
		this.userForm = this.userFB.group({
			username: ['', Validators.required],
			name: ['', Validators.required],
			email: ['', Validators.email],
      password: ['',Validators.required],
    //  Priviliedges: ['',Validators.required],
		});
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  onSubmit(){
    this.hasFormErrors = false;
    const controls = this.userForm.controls;
       console.log(controls);
    /** check form */
    if (this.userForm.invalid) {
      Object.keys(controls).forEach(controlName =>
        controls[controlName].markAsTouched()
      );

      this.hasFormErrors = true;
      return;
    }
  const user=this.prepareUsers();
   console.log(user)
  this.UserService.AddUser(user).subscribe(res=>{
    console.log(res);
       this.errorMessage=res['error'];
       if (res) {
        console.log(res);

        this.dialogRef.close({ res , isEdit: false });
      }
    
  })
//   this.onNoClick();
//  this.router.navigate(['/user-management/users'])
  
  }  



prepareUsers() {
  const controls = this.userForm.controls;
  const _user = new User();
 
  _user.username = controls['username'].value;
  _user.name = controls['name'].value;
  _user.password=controls['password'].value;
  _user.email=controls['email'].value;
  //_user.role=controls['Priviliedges'].value;

  return _user;

}

onAlertClose($event){
  this.hasFormErrors = false;
}
}