import { Component, OnInit, ChangeDetectorRef } from '@angular/core';

import { ViewBillingComponent } from '../user-management/users/_subs/billings/view-billing/view-billing.component'
import { BillingService } from '../../../core/auth/_services/billing.service';
import { Billing } from '../../../core/auth/_models/billing.model';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import {User} from '../../../core/auth/_models/user.model';
import { Router } from '@angular/router';
import { UserService } from '../../../core/auth/_services/user.service';
import { LayoutUtilsService, MessageType } from '../../../core/_base/crud';
import {UpdateBillingComponent} from '../user-management/users/_subs/billings/update-billing/update-billing.component'
import { MatPaginator, MatDialog, MatTableDataSource } from '@angular/material';
@Component({
  selector: 'kt-billing',
  templateUrl: './billing.component.html',
  styleUrls: ['./billing.component.scss']
})
export class BillingComponent implements OnInit {

  filterStatus: string = '';
  displayedColumns = ['ClubName', 'PackageName', 'StartDate', 'EndDate', 'Discount', 'status', 'actions'];
  clubName;
  billing: Billing;
  dataSource;
  club_id;
  NoDataMessage;
  billingForm: FormGroup;
  hasFormErrors: boolean = false;
  viewLoading: boolean = false;
  status;
  statusClass;


  constructor(private billingService: BillingService, 
    public dialog: MatDialog,
     private changeDetectorRefs: ChangeDetectorRef,
      private fb: FormBuilder, 
      private route: Router, 
      private layoutUtilsService: LayoutUtilsService,
      private userService: UserService) { }

  ngOnInit() {
    let now = new Date();
    let monthAgo = new Date();
    monthAgo.setMonth(now.getMonth() - 1);
    this.billingForm = this.fb.group({
      start: [monthAgo, [Validators.required]],
      end: [now, [Validators.required]],
    })
  }
  findBilling(start_date, end_date) {
    const controls = this.billingForm.controls;
    if (this.billingForm.invalid) {
      Object.keys(controls).forEach(controlName =>
        controls[controlName].markAsTouched()
      );
      this.hasFormErrors = true;
      return;
    }
    var d = new Date(start_date);
    start_date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
    var d2 = new Date(end_date);
    end_date = d2.getFullYear() + "-" + (d2.getMonth() + 1) + "-" + d2.getDate();
    this.billingService.getAllBills(start_date, end_date).subscribe(res => {
      let result = res['result'];
      let data :Billing [] = res['data'];
      if (result == true && data.length>0) {
        this.NoDataMessage = "";
        data.forEach(elm=>{
          this.userService.getUserbyId(elm.club).subscribe(res => {
            let user: User = res['users'][0];
            this.clubName = user.name;
            this.changeDetectorRefs.detectChanges();
          })
        })
        this.dataSource = this.dataSource = new MatTableDataSource<Billing>(data);
        console.log("datasource",this.dataSource);
        console.log("billing",this.billing);
        this.changeDetectorRefs.detectChanges();
      }
      else if (result == true && data.length == 0 ) {
        this.clubName = '';
        this.dataSource = '';
        this.NoDataMessage = "There is no invoice for this date";
        this.changeDetectorRefs.detectChanges();
        console.log('no data', this.NoDataMessage)
      }
      else if (result == false) {
        this.clubName = '';
        this.dataSource = '';
        this.NoDataMessage = "Insert valid date to show the invoice";
        this.changeDetectorRefs.detectChanges();
        console.log('no data', this.NoDataMessage)
      }

      console.log(res)
    })
  }
  viewBilling(billing) {
    const dialogRef = this.dialog.open(ViewBillingComponent, {
      width: '80%',
      height: '80%',
      data: { 'billing': billing, 'clubName': this.clubName }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }
  // changePaymentStatus(billing: Billing) {
  //   let method = "cash";
  //   var d = new Date();
  //   let date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate() + " " + d.getHours() + ":" + +d.getMinutes() + ":" + d.getSeconds();
  //   let bill_Key = billing._id
  //   this.billingService.createPayment(bill_Key,method,date).subscribe(res => {
  //     this.billing.payment_status='paid'
  //   });
  // }

  changePaymentStatus(billing: Billing) {
    const _saveMessage = `Membership successfully has been saved.`;
    const _messageType = billing._id ? MessageType.Update : MessageType.Create;
    const UpdateMessage = `Are you sure you want to update user billing status?`;
    const dialogRef = this.dialog.open(UpdateBillingComponent, { data: { billing: billing._id, UpdateMessage }, });
    dialogRef.afterClosed().subscribe(res => {
      if (!res) {
        return;
      } else {
        let method = "cash";
        var d = new Date();
        let date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate() + " " + d.getHours() + ":" + +d.getMinutes() + ":" + d.getSeconds();
        let bill_Key = billing._id
        this.billingService.createPayment(bill_Key, method, date).subscribe(res => {
        });
        this.billing.payment_status='paid'
        this.route.navigateByUrl('/billing');
        this.layoutUtilsService.showActionNotification(_saveMessage,_messageType);
        
      }
    })

  }



}
