// Angular
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
// RxJS
import { Observable } from 'rxjs';
import { map, catchError} from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';

// Environment
import { PublicMethods } from '../../_public/-public-methods'
import { publicService } from '../_services/public.service'
import { Membership } from '../_models/membership.model'


//const PublicMethods.createAdminURL = PublicMethods.createAdminURL
//const _url = PublicMethods.createURL


@Injectable({
  providedIn: 'root'
})



export class MembershipService {

    _urladmin = PublicMethods.createAdminURL
  constructor(private http: HttpClient, private publicservice: publicService) { }
  //user shared between component
  private membership = new BehaviorSubject("");
 


  getMemberships(pages=1,prepages=30,searchKey=null) {
    return this.http.get<Membership[]>(this._urladmin('membership', 'get', '','','',pages,prepages,searchKey))
  }
  editMemberships(_id,membership): Observable<any>{
   return this.http.patch(this._urladmin('membership', 'update',_id),membership)
  }
  deleteMembership(id,image): Observable<any>
  {
    return this.http.post(this._urladmin('membership', 'delete',id),{'image':image});
  }
  addMembership(membership): Observable<any>
  {
    return this.http.post(this._urladmin('membership', 'add',''),membership)
  }

  upload_file() {
    const API_getallactivities_URL = this._urladmin('membership', 'uploadimage', '', '', '');
   return API_getallactivities_URL;
  }
}
