// Angular
import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
// Material
import { MatButtonModule, MatFormFieldModule, MatInputModule, MatCheckboxModule } from '@angular/material';
// Translate
import { TranslateModule } from '@ngx-translate/core';
// NGRX
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
// CRUD
import { InterceptService } from '../../../core/_base/crud/';
// Module components
import { AuthComponent } from './auth.component';
import { LoginComponent } from './login/login.component';
import { EmailInvitationComponent } from './emailinvitation/emailinvitation.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { AuthNoticeComponent } from './auth-notice/auth-notice.component';
// Auth
import {  authReducer, AuthGuard, AuthEffects  } from '../../../core/auth';
import {AuthService} from '../../../core/auth/_services/auth.service'
import {LoggedGuard} from '../../../core/auth/_guards/logged.guard'
import {PermissionGuard} from '../../../core/auth/_guards/permission.guard'
const routes: Routes = [
	{
		path: '',
		component: AuthComponent,
		children: [
			{
				path: '',
				redirectTo: 'login',
				pathMatch: 'full'
			},
			{
				path: 'login',
				component: LoginComponent,
			//	canActivate:[LoggedGuard]
			},
			{
				path: 'emailinvitationlink/:parent_key/:email/:timestamp/:signature/:user_type/:all',
				component: EmailInvitationComponent
			},
			{
				path: 'forgot-password',
				component: ForgotPasswordComponent,
			//	canActivate:[LoggedGuard]
			}
		]
	}
];


@NgModule({
	imports: [
		CommonModule,
		FormsModule,
		ReactiveFormsModule,
		MatButtonModule,
		RouterModule.forChild(routes),
		MatInputModule,
		MatFormFieldModule,
		MatCheckboxModule,
		TranslateModule.forChild(),
		StoreModule.forFeature('auth', authReducer),
        EffectsModule.forFeature([AuthEffects])
	],
	providers: [
		InterceptService,
      	{
        	provide: HTTP_INTERCEPTORS,
       	 	useClass: InterceptService,
        	multi: true
      	},
	],
	exports: [AuthComponent],
	declarations: [
		AuthComponent,
		LoginComponent,
		EmailInvitationComponent,
		ForgotPasswordComponent,
		AuthNoticeComponent
	]
})

export class AuthModule {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: AuthModule,
            providers: [
				AuthService,
				AuthGuard,
                LoggedGuard
            ]
        };
    }
}
