// Angular
import { Injectable, assertPlatform } from '@angular/core';
import { HttpClient } from '@angular/common/http';


// RxJS
import { Observable, of, forkJoin } from 'rxjs';
import { map, catchError, mergeMap, tap } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs';


// Lodash
import { filter, some, find, each } from 'lodash';
// Environment
import { PublicMethods } from '../../_public/-public-methods'
import { publicService } from '../_services/public.service'
import { User } from '../_models/user.model'
//import { OneCustomerDeleted } from '../../e-commerce';

//const PublicMethods.createAdminURL = PublicMethods.createAdminURL
