@Injectable({
  providedIn: 'root'
})



export class UserService {

  constructor(private http: HttpClient, private publicservice: publicService) { }
  //user shared between component
  private user = new BehaviorSubject("");
  currentUser = this.user.asObservable();

  private role = new BehaviorSubject("");
  currentUserRole = this.role.asObservable();


  getallusers(pages, prepage, search, status = '', role = '', privilidge = '', orderby = '', order = -1) {
    var API_getAllUsers_URL = PublicMethods.createAdminURL('accounts', 'getallaccounts', '', '', '', pages, prepage, search)
    if (status != '') API_getAllUsers_URL += '&status=' + status;
    if (role != '') API_getAllUsers_URL += '&role=' + role;
    if (privilidge != '') API_getAllUsers_URL += '&privilidge=' + privilidge;
    if (orderby != '') API_getAllUsers_URL += '&orderby=' + orderby + '&order=' + order;
    return this.http.get<User[]>(API_getAllUsers_URL);
  }

  deleteUser(_id) {

    return this.http.delete(PublicMethods.createAdminURL('customers', 'deleteUserData', `${_id}/`, ''))
  }

  activateuser(ids, status) {
    return this.http.patch(PublicMethods.createAdminURL('accounts', 'updateAccountData', '', ''), { ids, status })
  }

  editUser() {

  }

  getallrole() {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('privilidgetypes', 'PrivilidgeType', '', '', '', 1, 1000)
    return this.http.get<any[]>(API_getAllUsers_URL)
  }
  // getUserPriviliges() {

  //   return this.http.post<any>(PublicMethods.createAdminURL('allprivilidges', 'getUsersPrivilidge', '', ''), JSON.parse(localStorage.getItem('user')['lastloginaccount']))
  // }

  //method to get user by public key
  changeuser(user) {
    this.user.next(user)
  }

  updateAdrressOfcurrentUser(address) {
    let user;
    this.currentUser.subscribe(res => {
      user = res
    })
    user.addressLine = address.addressLine;
    user.city = address.city;
    user.state = address.state;
    user.postCode = address.postCode;
  }

  updateSocialNetworksOfcurrentUser(socialNetwok) {
    let user;
    this.currentUser.subscribe(res => {
      user = res
    })

    user.faceboook = socialNetwok.faceboook;
    user.instagram = socialNetwok.instagram;
    user.linkedIn = socialNetwok.linkedIn;
    user.twitter = socialNetwok.twitter;
  }

  updateRolesOfcurrentUser(privilidge, typeOfRole) {
    let user;
    this.currentUser.subscribe(res => {
      console.log(res);
      user = res
    })
    user.role = { privilidge, typeOfRole }
  }

  //method to update data of user params basic information {username,mail,name} and metaData
  updateUserData(publicKey,user) {
    console.log(PublicMethods.createAdminURL('accounts', 'updateAccountData',publicKey ,''))

    return this.http.patch(PublicMethods.createAdminURL('accounts', 'updateAccountData', user['pub_key'], ''), user)
  }

  AddUser(user){
    return this.http.post(PublicMethods.createAdminURL('accounts', 'add', ''),user)
  }
  // updateUserPrvilidge(id, usersPrivilidge) {
  //   console.log(id, usersPrivilidge)

  //   return this.http.patch(PublicMethods.createAdminURL('allprivilidges', 'UsersPrivilidge', id, ''), usersPrivilidge)
  // }
  // AddUserPrivilidge(privilidge, type, key) {
  //   let obj;
  //   if (type)
  //     obj = { privilidge, type, key }
  //   else
  //     obj = { privilidge, key }
  //   this.publicservice.checkResponsePostMetthod(PublicMethods.createAdminURL('allprivilidges', 'UsersPrivilidge', '', ''), obj)
  //   return this.http.post(PublicMethods.createURL('allprivilidges', 'UsersPrivilidge', '', ''), obj)
  // }


  getAllSettings(pages, prepage) {

    const API_getAllUsers_URL = PublicMethods.createAdminURL('options', 'getAllSettings', '', '', '', pages, prepage)

    return this.http.get<any[]>(API_getAllUsers_URL)
  }

  updateSettings(data) {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('options', 'updatesettings', '', '')

    return this.http.patch<any[]>(API_getAllUsers_URL, data)
  }

  getAllInvitations(pages, perpage, search, status, author_key, orderby, order) {
    var API_getAllUsers_URL = PublicMethods.createAdminURL('emails', 'getEmails', '', '', '', pages, perpage, search)
    if (status != '') API_getAllUsers_URL += '&status=' + status;
    if (author_key != '') API_getAllUsers_URL += '&author_key=' + author_key;
    if (orderby != '') API_getAllUsers_URL += '&orderby=' + orderby + '&order=' + order;
    return this.http.get<any[]>(API_getAllUsers_URL)
  }

  getAllBrachsAndUnits(pages, perpages) {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('accounts', 'getAllBranchAndUnits', '', '', '', pages, perpages)

    return this.http.get<any[]>(API_getAllUsers_URL)
  }



  getUsresWhichSendEmails() {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('emails', 'getUsresWhichSendEmails', '', '')

    return this.http.get<any[]>(API_getAllUsers_URL)
  }

  sendInvitations(data) {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('accounts', 'invitation', '', '')

    return this.http.post<any[]>(API_getAllUsers_URL, data)
  }

  reSendInvitations(id) {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('accounts', 'resendinvitation', id, '')

    return this.http.get<any[]>(API_getAllUsers_URL)
  }

  reVokeInvitations(id) {
    const API_getAllUsers_URL = PublicMethods.createAdminURL('accounts', 'revokeinvitation', id, '');

    return this.http.get<any[]>(API_getAllUsers_URL);
  }
  switchAccount(key): Observable<any>{
    const API_switchAccount_URL = PublicMethods.createAdminURL('accounts', 'changeaccount', key)
    console.log(API_switchAccount_URL, PublicMethods.createHeaders())
    return this.http.get<User[]>(API_switchAccount_URL)
      .pipe(
        map((res: User[]) => {
          return res;
        }),
        catchError(err => {
          return null;
        })
      );
  }
  //staf
  getUserbyId(key): Observable<any> {
    return this.http.get<User[]>(PublicMethods.createAdminURL('accounts', 'getaccountBypublickkey', key))
  }

  addPrivildge(data)
  {
    return this.http.post(PublicMethods.createAdminURL('accountprivilidge','UsersPrivilidge',''),data)
  }
  updateUserPrivildge(id,data){
    return this.http.patch(PublicMethods.createAdminURL('accountprivilidge','UsersPrivilidge',id),data)
  }

}
