// NGRX
import { Action } from '@ngrx/store';
import { Update } from '@ngrx/entity';
// CRUD
import { QueryParamsModel } from '../../_base/crud';
// Models
import { Membership } from '../_models/membership.model';

export enum MembershipActionTypes {
    AllMembershipRequested = '[Membership Home Page] All Membership Requested',
    AllMembershipLoaded = '[Membership API] All Membership Loaded',
    MembershipOnServerCreated = '[Edit Membership Dialog] Membership On Server Created',
    MembershipCreated = '[Edit Membership Dialog] Membership Created',
    MembershipUpdated = '[Edit Membership Dialog] Membership Updated',
    MembershipDeleted = '[Membership List Page] Membership Deleted',
    MembershipPageRequested = '[Membership List Page] Membership Page Requested',
    MembershipPageLoaded = '[Membership API] Membership Page Loaded',
    MembershipPageCancelled = '[Membership API] Membership Page Cancelled',
    MembershipPageToggleLoading = '[Membership page] Membership Page Toggle Loading',
    MembershipActionToggleLoading = '[Membership] Membership Action Toggle Loading'
}

export class MembershipOnServerCreated implements Action {
    readonly type = MembershipActionTypes.MembershipOnServerCreated;
    constructor(public payload: { membership: Membership }) { }
}

export class MembershipCreated implements Action {
    readonly type = MembershipActionTypes.MembershipCreated;
    constructor(public payload: { role: Membership }) { }
}

export class MembershipUpdated implements Action {
    readonly type = MembershipActionTypes.MembershipUpdated;
    constructor(public payload: {
        partialmembership: Update<Membership>,
       membership: Membership
    }) { }
}

export class MembershipDeleted implements Action {
    readonly type = MembershipActionTypes.MembershipDeleted;
    constructor(public payload: { id: string }) {}
}

export class MembershipPageRequested implements Action {
    readonly type = MembershipActionTypes.MembershipPageRequested;
    constructor(public payload: { page: QueryParamsModel }) { }
}

export class MembershipPageLoaded implements Action {
    readonly type = MembershipActionTypes.MembershipPageLoaded;
    constructor(public payload: { Membership: Membership[], totalCount: number, page: QueryParamsModel }) { }
}

export class MembershipPageCancelled implements Action {
    readonly type = MembershipActionTypes.MembershipPageCancelled;
}

export class AllMembershipRequested implements Action {
    readonly type = MembershipActionTypes.AllMembershipRequested;
}

export class AllMembershipLoaded implements Action {
    readonly type = MembershipActionTypes.AllMembershipLoaded;
    constructor(public payload: { Membership: Membership[] }) { }
}

export class MembershipPageToggleLoading implements Action {
    readonly type = MembershipActionTypes.MembershipPageToggleLoading;
    constructor(public payload: { isLoading: boolean }) { }
}

export class MembershipActionToggleLoading implements Action {
    readonly type = MembershipActionTypes.MembershipActionToggleLoading;
    constructor(public payload: { isLoading: boolean }) { }
}

export type RoleActions = MembershipCreated
| MembershipUpdated
| MembershipDeleted
| MembershipPageRequested
| MembershipPageLoaded
| MembershipPageCancelled
| AllMembershipLoaded
| AllMembershipRequested
| MembershipOnServerCreated
| MembershipPageToggleLoading
| MembershipActionToggleLoading;
