// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router'; 
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
// NGRX
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
// Translate
import { TranslateModule } from '@ngx-translate/core';
import { PartialsModule } from '../../partials/partials.module';
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService} from '../../../core/_base/crud';
// Shared
import { ActionNotificationComponent } from '../../partials/content/crud';
import { CodePreviewModule } from '../../partials/content/general/code-preview/code-preview.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
// Components
import { UserManagementComponent } from './user-management.component';
import { UsersListComponent } from './users/users-list/users-list.component';
import { UserEditComponent } from './users/user-edit/user-edit.component';
import { RolesListComponent } from './roles/roles-list/roles-list.component';
import { InvitationComponent } from './invitations/invitations.component';
import { RoleEditDialogComponent } from './roles/role-edit/role-edit.dialog.component';
import { InvitationsEditDialogComponent } from './invitations/invitations-edit/invitations-edit.dialog.component';

import { UserRolesListComponent } from './users/_subs/user-roles/user-roles-list.component';
import { AddressComponent } from './users/_subs/address/address.component';
import { SocialNetworksComponent } from './users/_subs/social-networks/social-networks.component';

//guard
import {PermissionGuard} from '../../../core/auth/_guards/permission.guard'
// Material
import {
	MatInputModule,
	MatPaginatorModule,
	MatProgressSpinnerModule,
	MatSortModule,
	MatTableModule,
	MatSelectModule,
	MatMenuModule,
	MatProgressBarModule,
	MatButtonModule,
	MatCheckboxModule,
	MatDialogModule,
	MatTabsModule,
	MatNativeDateModule,
	MatCardModule,
	MatRadioModule,
	MatIconModule,
	MatDatepickerModule,
	MatExpansionModule,
	MatTreeModule,
	MatAutocompleteModule,
	MAT_DIALOG_DEFAULT_OPTIONS,
	MatSnackBarModule,
	MatTooltipModule
} from '@angular/material';
import {
	usersReducer,
	UserEffects
} from '../../../core/auth';
import { BillingsComponent } from './users/_subs/billings/billings.component';
import { ViewBillingComponent } from './users/_subs/billings/view-billing/view-billing.component';
import { UpdateBillingComponent } from './users/_subs/billings/update-billing/update-billing.component';
import { AddUserComponent } from './users/add-user/add-user.component';



const routes: Routes = [
	{
		path: '',
		component: UserManagementComponent,
		children: [
			{
				path: '',
				redirectTo: 'roles',
				pathMatch: 'full',
				data:{title:'roles'},
                canActivate:[PermissionGuard]

			},
			{
				path: 'roles',
				component: RolesListComponent,
				data:{title:'roles'},
		  	    canActivate:[PermissionGuard]
			},
			{
				path: 'invitations',
				component: InvitationComponent,
				data:{title:'invitations'},
				canActivate:[PermissionGuard]
			}
			,
			{
				path: 'invitation',
				component: InvitationComponent,
				data:{title:'invitations'},
				canActivate:[PermissionGuard]
			},
			{
				path: 'billing',
				component: BillingsComponent,
				data:{title:'billing'},
				canActivate:[PermissionGuard]
			},
			{
				path: 'users',
				component: UsersListComponent
				,data:{title:'users'},
                canActivate:[PermissionGuard]
			},{
				path: 'user',
				component: UsersListComponent
				,data:{title:'users'},
                canActivate:[PermissionGuard]
			},
			{
				path: 'users/:id',
				component: UsersListComponent
				,data:{title:'users'},
                canActivate:[PermissionGuard]
			},
			{
				path: 'users/add',
				component: UserEditComponent
			},
			{
				path: 'users/add:id',
				component: UserEditComponent
				,data:{title:'addUser'},
                canActivate:[PermissionGuard]
			},
			{

				path: 'users/edit',
				component: UserEditComponent
				,data:{title:'editUser'},
                canActivate:[PermissionGuard]
			},
			{
				path: 'users/edit/:id',
				component: UserEditComponent
				,data:{title:'editUser'},
                canActivate:[PermissionGuard]
			},
			{
				path: 'users/:id/edit/:id',
				component: UserEditComponent
				,data:{title:'editUser'},
                canActivate:[PermissionGuard]
			},
			{
				path: 'users/:id/edits/:id',
				component: UserEditComponent
				,data:{title:'editUser'},
                canActivate:[PermissionGuard]
			},
		]
	}
];

@NgModule({
	imports: [
		CommonModule,
		HttpClientModule,
		PartialsModule,
		RouterModule.forChild(routes),
		StoreModule.forFeature('users', usersReducer),
        EffectsModule.forFeature([UserEffects]),
		FormsModule,
		ReactiveFormsModule,
		TranslateModule.forChild(),
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
        MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatExpansionModule,
		MatTabsModule,
		MatTooltipModule,
		MatDialogModule,
		CodePreviewModule,
		MaterialPreviewModule,
		MatTreeModule,
	],
	providers: [
		InterceptService,
		{
        	provide: HTTP_INTERCEPTORS,
       	 	useClass: InterceptService,
			multi: true
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: 'kt-mat-dialog-container__wrapper',
				height: 'auto',
				width: '900px'
			}
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
		PermissionGuard
	],
	entryComponents: [
		ViewBillingComponent,
		ActionNotificationComponent,
		RoleEditDialogComponent,
		InvitationsEditDialogComponent,
		UpdateBillingComponent,
		AddUserComponent,
	],
	declarations: [
		UserManagementComponent,
		UsersListComponent,
		UserEditComponent,
		RolesListComponent,
		InvitationComponent,
		RoleEditDialogComponent,
		UserRolesListComponent,
		AddressComponent,
		SocialNetworksComponent,
		InvitationsEditDialogComponent,
		
		BillingsComponent,
		ViewBillingComponent,
		UpdateBillingComponent,
		AddUserComponent,

	]
})
export class UserManagementModule {}
