// Angular
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
// RxJS
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
// NGRX
import { select, Store } from '@ngrx/store';
// Auth reducers and selectors
import { AppState} from '../../../core/reducers/';
import {PermissionsService} from '../../../core/auth/_services/permissions.sevice'
import {UserService} from '../../../core/auth/_services/user.service'
import { permissionsReducer } from '../_reducers/permission.reducers';
@Injectable()


export class PermissionGuard implements CanActivate {

    constructor(private store: Store<AppState>, private router: Router,private permission:PermissionsService,private user:UserService) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): any{
        const moduleName = route.data['title'] as string;

  let permissions = JSON.parse(localStorage.getItem('user'))['allPrivilidge'];
  console.log(moduleName,permissions,this.permission['Roles'])
        if( ! this.checked())
        {

            this.router.navigateByUrl('/error/403');
        }
        else
        {
            if(moduleName=='roles')
            {
              if(! this.checkedPermission(permissions['Roles']) )
              this.router.navigateByUrl('/error/403');
              if(! permissions['Roles']['Get Roles'] )
              this.router.navigateByUrl('/error/403');

            }
            else if(moduleName=='users')
            {
                if(! this.checkedPermission(['Users Mangement']))
                this.router.navigateByUrl('/error/403');
                if(! permissions['Users Mangement']['Get All Users'])
                this.router.navigateByUrl('/error/403');
            }
            else if(moduleName=='invitation')
            {
                if(!permissions['Users Mangement']['Invitation'])
                this.router.navigateByUrl('/error/403');
            }
            else if (moduleName=='editUser')
            {
                  this.user.currentUser.subscribe(res=>{
                      if(!res)
                      {
                      this.router.navigateByUrl('user-management/users')
                      }
                  })
                  if(! permissions['Users Mangement']['Update User'])
                  this.router.navigateByUrl('/error/403')
            }
            else if (moduleName=='setting')
            {
                if(! permissions['Users Mangement']['Setting'])
                this.router.navigateByUrl('/error/403')

            }
            else if(moduleName=='addUser')
            {
                if(! permissions['Users Mangement']['Setting'])
                this.router.navigateByUrl('/error/403')
            }
        }

            return true;

    }
checked()
{
    let length=0
    if(localStorage.getItem('user'))
    {
    Object.values(JSON.parse(localStorage.getItem('user'))['allPrivilidge']).forEach(function(elem){
        length +=Object.values(elem).length
        console.log(Object.values(elem).length )
    })

    }
    return length;
}
checkedPermission(object)
{

    let length=0
    if(object)
    {
    Object.values(object).forEach(function(elem){
        length +=Object.values(elem).length
        console.log(Object.values(elem).length )
    })

    }
    return length;
}
}
