import { BaseModel } from '../../_base/crud';

export class Role extends BaseModel {
    id: number;
    title: string;
    type:string;
    default:string;
    permissions: number[];
    isCoreRole: boolean = false;

    clear(): void {
        this.id = undefined;
        this.title = '';
        this.type='';
        this.permissions = [];
        this.isCoreRole = false;
        this.default=''
	}
}
