import { Component, OnInit, Inject, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators,FormArray } from '@angular/forms';
// Material
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// RxJS
import { Subscription } from 'rxjs';
// NGRX

// import { Store } from '@ngrx/store';
import { Membership } from '../../../../core/auth';
import { AppState } from '../../../../core/reducers';
import { MembershipService } from '../../../../core/auth/_services/membership.service';
import { FileUploader } from 'ng2-file-upload';
import { Ng2ImgMaxService } from 'ng2-img-max';
import { currentUserCalculation } from '../../../../core/auth/_models/membership.model'



@Component({
  selector: 'kt-edit-membership',
  templateUrl: './edit-membership.component.html',
  styleUrls:['./edit-membership.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class EditMembershipComponent implements OnInit, OnDestroy {
  MaxCurrentMembers = ['overall the account', 'within the branch'];
  supportWay = ['mobile', 'chat'];
  membership: Membership;
  membershipForm: FormGroup;
  hasFormErrors: boolean = false;
  viewLoading: boolean = false;
  maxCurrentUser: FormGroup;
  maxCurrentUserLimited: boolean;
  keys = ['Newsubscribe', 'NewMembershipsold', 'Membershipupgrade', 'MembershipDowngrade', 'Membershipcancle', 'PTAttendancSchedule', 'PTAttendancreSchedule', 'PTAttendancCancle', 'PTAttendancScheduleRefund', 'MembershipRefund']
  //currentUserCalculation: FormArray;
  baseUrl;
  //image
  private token = JSON.parse(localStorage.getItem('user'))['token'];
  public uploader: FileUploader = new FileUploader({ url: this.MembershipService.upload_file(), authToken: this.token, itemAlias: 'upload' });
  public imageprivew: any;
  // Private properties
  private componentSubscriptions: Subscription;
  get currentUserCalculation(): FormArray {
    return this.membershipForm.get('currentUserCalculation') as FormArray;
  }
  name;
  weight;
  calc;
  trans_key_state;
  constructor(public dialogRef: MatDialogRef<EditMembershipComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: FormBuilder,
    private changeDetectorRefs: ChangeDetectorRef,
    // private store: Store<AppState>,
    private MembershipService: MembershipService,
    private _ng2ImgMax: Ng2ImgMaxService
  ) { }

  ngOnInit() {
    this.membership = this.data['membership'];
    this.baseUrl = this.data.Url;
    this.createForm();
    if(this.membership.currentUserCalculation.length){
      for(let i=0;i <=this.membership.currentUserCalculation.length-1;i++){
             // this.currentUserCalculation.push(this.getCurrentUserCal());
              this.name=this.membership.currentUserCalculation[i].name;
              this.trans_key_state=this.membership.currentUserCalculation[i].trans_key_state;
              this.calc=this.membership.currentUserCalculation[i].calc;
              this.weight=this.membership.currentUserCalculation[i].weight;
              this.currentUserCalculation.push(this.getCurrentUserCal());
      }
    }
    if(this.membership.image!=""){
    this.imageprivew =this.baseUrl+this.membership.image;
  } else{this.imageprivew=''}
    this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };

     this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
      console.log("ImageUpload:uploaded:", item, status, response);
     };
    console.log(this.imageprivew)
  }
  ngOnDestroy() {
    if (this.componentSubscriptions) {
      this.componentSubscriptions.unsubscribe();
    }
  }


  onSubmit(input_image) {
    this.hasFormErrors = false;
    const controls = this.membershipForm.controls;
    /** check form */
    if (this.membershipForm.invalid) {
      Object.keys(controls).forEach(controlName =>
        controls[controlName].markAsTouched()
      );

      this.hasFormErrors = true;
      return;
    }
    let editedMembership = this.prepareMemberships();
        //     update
    if (this.data.membership.title) {
      if (input_image != '') {
        console.log('updaaaaaaaaaaaaaaaate')
        console.log(this.imageprivew);
        //(this.membership['image']!='')? (editedMembership['old_image']=this.membership['image']) :'';
           editedMembership['old_image'] = this.membership.image
       // console.log(this.membership['old_image'])
        this.uploader.onBuildItemForm = (fileItem: any, form: any) => {
          form.append('old_image', this.membership.image);
        };
        this.uploader.uploadAll();
        console.log('iam here now')
        //error hereeeeeeeeeeeeeeee              
        this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
          var responsePath = JSON.parse(response);
          console.log( 'response',response);
          console.log('responsePath',responsePath);
          if (responsePath.result) {
            // this.workout.image_url = responsePath.image;
            editedMembership['image'] = responsePath.data;
            console.log(editedMembership['image'])
            console.log(editedMembership)
            this.updateMembership(editedMembership);
             console.log('updateImage', editedMembership)
          }
        }
      }
      else {
        console.log('update')
        this.updateMembership(editedMembership)
        console.log('update', editedMembership)
      }
    } else {
      (this.membership['image'] != '') ? (editedMembership['image'] = this.membership['image']) : '';
      if (this.imageprivew != '') {
        this.uploader.uploadAll();
        this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
          var responsePath = JSON.parse(response);
          if (responsePath.result) {
            // this.membership.image = responsePath.image;
            editedMembership['image'] = responsePath.data;
            this.createMembership(editedMembership);
            console.log('createImage', editedMembership)
            console.log('createImage', editedMembership['image'])
          } else {
            console.log('error when upload image')
          }
        };
      }
      else {
        this.createMembership(editedMembership)
        console.log('create', editedMembership)
      }
      
    }

  }
  updateMembership(membership) {

    this.MembershipService.editMemberships(membership._id, membership).subscribe(res => {
      if (res) {
        console.log(res);

        this.dialogRef.close({ membership, isEdit: true });
      }
    })
    console.log(membership)
  }
  createMembership(membership: Membership) {
    // this.store.dispatch(new MembershipOnServerCreated({ membership: membership}));
    this.MembershipService.addMembership(membership).subscribe((res) => {
      this.dialogRef.close({ membership, isEdit: false });
    })

  }
  getTitle(): string {
    if (this.membership._id) {
      return `Edit membership '${
        this.membership.title
        }'`;
    }

    return 'New membership';

  }
  createForm() {
    this.membershipForm = this.fb.group({
      title: [this.membership.title, Validators.required],
      description: [this.membership.description, Validators.required],
      maxCurrentUser: this.fb.group({
        allBranches: [this.membership.maxCurrentUser.allBranches, Validators.required],
        limited: [this.membership.maxCurrentUser.limited, Validators.required],

      }),

      currentUserCalculation: this.fb.array([
       // this.getCurrentUserCal()
      ]),
      MaxBranchNumber: [this.membership.maxBranchNumber, Validators.required],
      MaxUnitsNumber: [this.membership.maxUnitsNumber, Validators.required],
      billingMethod: [this.membership.billingMethod, Validators.required],
      supportRepondingPeriod: [this.membership.supportRepondingPeriod, Validators.required],
      fees: [this.membership.fees, Validators.required],
      supportWay: [this.membership.supportWay, Validators.required],
      discount: [this.membership.discount, Validators.required],
      fees_percent: [this.membership.fees_percent, Validators.required],
      trans_max_num: [this.membership.trans_max_num, Validators.required],
    });
    console.log(this.membershipForm)

  }


  addItem(): void {
    // this.currentUserCalculation = this.membershipForm.get('currentUserCalculation') as FormArray;
    this.currentUserCalculation.push(this.getCurrentUserCal());
  }



  getCurrentUserCal(): FormGroup {

    return this.fb.group({
      name: [this.name, Validators.required],
      trans_key_state: [this.trans_key_state, Validators.required],
      weight: [this.weight, Validators.required],
      calc: [this.calc, Validators.required],
    })

  }


  prepareMemberships(): Membership {
    //this.prepareMaxUsers();
    const controls = this.membershipForm.controls;
    const _membership = new Membership();
    _membership._id = this.membership._id;
    _membership.title = controls['title'].value;
    _membership.description = controls['description'].value;
    _membership.maxCurrentUser = controls['maxCurrentUser'].value;
    //_membership.maxCurrentUser.allbranchs=cont['allBranches'].value;
    _membership.maxBranchNumber = controls['MaxBranchNumber'].value;
    _membership.currentUserCalculation = controls['currentUserCalculation'].value;
    _membership.billingMethod = controls['billingMethod'].value;
    _membership.supportWay = controls['supportWay'].value;
    _membership.supportRepondingPeriod = controls['supportRepondingPeriod'].value;
    _membership.fees = controls['fees'].value;
    _membership.maxUnitsNumber = controls['MaxUnitsNumber'].value;
    _membership.discount = controls['discount'].value;
    _membership.fees_percent = controls['fees_percent'].value;
    _membership.trans_max_num = controls['trans_max_num'].value;
    
    return _membership;

  }


  onAlertClose($event) {
    this.hasFormErrors = false;
  }

/**
 * @param $event: Event
 */
  handleUpload(event: any) {
    if (event.target.files && event.target.files[0]) {
      this.viewLoading = true;
      var reader = new FileReader();
      // reader.onload = (event: ProgressEvent) => {this.imageprivew = (<FileReader>event.target).result;}
      // reader.readAsDataURL(event.target.files[0]);

      this._ng2ImgMax.resizeImage(event.target.files[0], 600, 600).subscribe(
        result => {
          const newImage = new File([result], result.name);
          this.uploader.clearQueue();
          this.uploader.addToQueue([newImage]);
          //this.uploader.uploadAll();
          //console.log(result);
          reader.onload = (event: ProgressEvent) => {this.imageprivew = (<FileReader>event.target).result;
             this.changeDetectorRefs.detectChanges();
          }
          reader.readAsDataURL(event.target.files[0]);
         
          this.viewLoading = false;
          
        },
        error => console.log(error)
      );
    }
    console.log(this.viewLoading)
    
  }
  onNoClick(): void {
    this.dialogRef.close();
  }

 

}
