import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//http
import { HttpClient } from '@angular/common/http'
//url
import { PublicMethods } from '../../_public/-public-methods'
@Injectable({
  providedIn: 'root'
})
export class PermissionsService {

  constructor(
    private http: HttpClient,

  ) { }

  //shared current user privildegs
  private userPrviliedges = JSON.parse(localStorage.getItem('user'))['allPrivilidge'];
  //create url
  
  _urladmin = PublicMethods.createAdminURL
  preparePrivilidges() {
     if(this.userPrviliedges)
     {
    const dataPrivlidgesSource = [{}]
    Object.keys(this.userPrviliedges).forEach((title, id) => {
      id += 1
      dataPrivlidgesSource.push({ id, title, _children: this.prepareChildPermission(id - 1), isSelected: false })

    })



    return dataPrivlidgesSource.slice(1, dataPrivlidgesSource.length - 1);
  }
  }
  prepareChildPermission(index) {
    const child = []

    Object.keys(Object.values(this.userPrviliedges)[index]).forEach((key, id) => {
      const value = Object.values(Object.values(this.userPrviliedges)[index])[id];
      if (key && value)
        child.push({ key, value, isSelected: false })

    })
    if (child.length > 0)
      return child;
    else
      return [{ "title": '' }]
  }

  getPriviliedgesType(pages=1,prepages=30,searchKey=null) {
    return this.http.get(this._urladmin('privilidgetypes', 'PrivilidgeType', '','','',pages,prepages,searchKey))
  }
  editPrivilagesType(id,allPrivilidge,type,defaultvalue){
   return this.http.patch(this._urladmin('privilidgetypes', 'PrivilidgeType',id),{allPrivilidge,type,default:defaultvalue})
  }
  deletePriviliages(id)
  {
    return this.http.delete(this._urladmin('privilidgetypes', 'PrivilidgeType',id))
  }
  addPriviliages(allPrivilidge,type,defaultvalue)
  {
    return this.http.post(this._urladmin('privilidgetypes', 'PrivilidgeType',''),{allPrivilidge,type,default:defaultvalue})
  }
}
