    // https://mygymin.herokuapp.com/
    static createAdminURL(module, service, account_key, pub_key = null, parent_key = null, pages = 1, prepage = 30, search = null,unit_key=null,branch_key=null,type=null,membership_id=null,downgrade=false,upgrade=false,filtertime=null) {
        let _timeStamp = Date.now(),
            _signature = Md5.hashStr((Md5.hashStr(`gyminAppwsds548_$%#@${_timeStamp}@#!$$#@#$844%^^&(SDF%*%)`)).toString()),
            _url = `https://mygymin.herokuapp.com/api/admin/v1/${module}/${service}/${account_key}?timestamp=${_timeStamp}&signature=${_signature}`
        if (localStorage.getItem('user')) {
            var parent_key = (JSON.parse(localStorage.getItem('user')))['pub_key'];
            var public_key = (JSON.parse(localStorage.getItem('user')))['pub_key'];
            if(parent_key == 'undefined') parent_key = public_key;
            _url += `&public_key=${public_key}&parent_key=${parent_key}` 
        }
        if (pub_key) {
            _url += `&public_key=${pub_key}`
        }
        if (pages && prepage) {
            _url += `&page=${pages}&perpage=${prepage}`
        }
        if (search) {
            _url += `&search=${search}`
        }
        if(unit_key)
        {
            _url +=`&unit_key=${unit_key}`
        }
        if(type)
        {
            _url +=`&type=${type}`
        }
        if(membership_id)
        {
            _url +=`&membership_id=${membership_id}`
        }
        if(downgrade)
        {
            _url +=`&downgrade=${true}`
        }
        if(upgrade)
        {
            _url +=`&upgrade=${true}`
        }
        if(filtertime)
        {
            _url +=`&filtertime=${filtertime}`
        }
        return _url;
    }

   
    static createHeaders() {
        const httpHeaders = new HttpHeaders();
        httpHeaders.set('Content-Type', 'application/x-www-form-urlencoded');
        httpHeaders.append('Authorization', 'Bearer ' + localStorage.getItem(environment.authTokenKey));
        console.log(httpHeaders)
        return httpHeaders
    }
    static convertLocalTimeToUTC(date)
    {
        let d = new Date(date);
        let localTime = d.getTime();
        let localOffset = d.getTimezoneOffset() * 60000;
        let utc = localTime + localOffset;
        return utc;
    }
    static convertUTCToLocalTime(date,offset)
    {
       let d = new Date(date);
       let localTime = d.getTime();
       let localOffset = d.getTimezoneOffset() * 60000;
       let utc = localTime + localOffset;
       return utc + (3600000*offset);
    }
