

interface MaxCurrentUser {
    allBranches: number;
    limited: string;
   
}

export class currentUserCalculation {
    name: string;
    trans_key_state: string;
    weight: number;
    calc: number;
}
export class Membership {
    constructor() {
        this._id = "";
        this.increment = 0;
        this.title = "";
        this.description = "";
        this.fees = 0;
        this.maxCurrentUser = { limited: '', allBranches: 0 };
        this.currentUserCalculation = [{name:'',trans_key_state:'',weight:0,calc:0}];
        this.maxBranchNumber = 0;
        this.maxUnitsNumber = 0;
        this.billingMethod = '';
        this.supportWay = '';
        this.supportRepondingPeriod = '';
        this.discount = 0;
        this.status = false;
        this.fees_percent=0;
        this.trans_max_num=0;
        this.image='';
    }
    increment: number;
    _id: string;
    title: string;
    description: string;
    fees: number;
    maxCurrentUser: MaxCurrentUser;
    currentUserCalculation: currentUserCalculation [];
    maxBranchNumber: number;
    maxUnitsNumber: number;
    billingMethod: string;
    supportWay: string;
    supportRepondingPeriod: string;
    discount: number;
    status: boolean;
    fees_percent:number;
    trans_max_num:number;
    image:string;
}

