@Component({
	selector: 'kt-roles-list',
	templateUrl: './roles-list.component.html',
	styleUrls: ['../../usser.management.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush

})
export class RolesListComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource: MatTableDataSource<any>;
	SearchKey;

	displayedColumns = ['type','default', 'actions'];
	@ViewChild(MatPaginator,{static:true}) paginator: MatPaginator;
	//	@ViewChild('sort1') sort: MatSort;
	// Filter fields
	@ViewChild('searchInput',{read: ElementRef,static:true}) searchInput: ElementRef;
	// Selection
	selection = new SelectionModel<Role>(true, []);
	rolesResult: Role[] = [];

	// Subscriptions
	private subscriptions: Subscription[] = [];

	/**
	 * Component constructor
	 *
	 * @param store: Store<AppState>
	 * @param dialog: MatDialog
	 * @param snackBar: MatSnackBar
	 * @param layoutUtilsService: LayoutUtilsService
	 */
	constructor(
		private store: Store<AppState>,
		public dialog: MatDialog,
		public snackBar: MatSnackBar,
		private layoutUtilsService: LayoutUtilsService,
		private permission: PermissionsService,
		private changeDetectorRefs: ChangeDetectorRef 
	) { }

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() { 
		this.LoadPriviliages();
		// const searchSubscription = fromEvent(this.searchInput.nativeElement, 'keyup').pipe(
		// 	// tslint:disable-next-line:max-line-length
		// 	debounceTime(50), // The user can type quite quickly in the input box, and that could trigger a lot of server requests. With this operator, we are limiting the amount of server requests emitted to a maximum of one every 150ms
		// 	distinctUntilChanged(), // This operator will eliminate duplicate values
		// 	tap(() => {
		// 		this.paginator.pageIndex = 0;
		// 		this.LoadPriviliages();
		// 	})
		// )
		// .subscribe();
		// this.subscriptions.push(searchSubscription);	
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	
	filterConfiguration(): any {
		const filter: any = {};
		const searchText: string = this.searchInput.nativeElement.value;
		filter.title = searchText;
		return filter;
	}

	/** ACTIONS */
	/**
	 * Delete role
	 *
	 * @param _item: Role
	 */
	deleteRole(_item) {
		const _title: string = 'User Role';
		const _description: string = 'Are you sure to permanently delete this role?';
		const _waitDesciption: string = 'Role is deleting...';
		const _deleteMessage = `Role has been deleted`;
		

			const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
			dialogRef.afterClosed().subscribe(res => {
				if (!res) {
					return;
				}else{
					this.permission.deletePriviliages(_item.id).subscribe(res => {
						console.log(res)
				this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
				
			})
			this.LoadPriviliages()
		}
		
			//this.store.dispatch(new RoleDeleted({ id: _item.id }));

		});
	}

	/** Fetch */
	/**
	 * Fetch selected rows
	 */
	fetchRoles() {
		const messages = [];
		this.selection.selected.forEach(elem => {
			messages.push({
				text: `${elem.title}`,
				id: elem.id.toString(),
				// status: elem.username
			});
		});
		this.layoutUtilsService.fetchElements(messages);
	}

	/**
	 * Add role
	 */
	addRole() {
		const role = { id: null, title: null, _childern: [], isSelected: false }
		this.editRole(role);
	}

	/**
	 * Edit role
	 *
	 * @param role: Role
	 */
	editRole(role) {

		const _saveMessage = `Role successfully has been saved.`;
		const _messageType = role.id ? MessageType.Update : MessageType.Create;
		const dialogRef = this.dialog.open(RoleEditDialogComponent, { data: { role } });
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {

				return;
			}

			this.layoutUtilsService.showActionNotification(_saveMessage, _messageType, 10000, true, true);
			//this.loadRolesList();
			this.LoadPriviliages()
		});
	}

	/**
	 * Check all rows are selected
	 */
	isAllSelected(): boolean {
		const numSelected = this.selection.selected.length;
		const numRows = this.rolesResult.length;
		return numSelected === numRows;
	}

	/**
	 * Toggle selection
	 */
	masterToggle() {
		if (this.selection.selected.length === this.rolesResult.length) {
			this.selection.clear();
		} else {
			this.rolesResult.forEach(row => this.selection.select(row));
		}
	}
	applyFilter(filterValue: string) {
		filterValue = filterValue.trim(); // Remove whitespace
		filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
		this.dataSource.filter = filterValue;
	  }
	//	this.dataSource=new MatTableDataSource(roles)

	splitPriviliage(priviliageString) {
		let priviliadgeList = []
		if (priviliageString) {
			let priviliadge = priviliageString.split(',')
			priviliadge.forEach((item) => {
				priviliadgeList.push({ title: item, isSelected: false })
			})
		}
		return priviliadgeList;
	}
	LoadPriviliages(pages = null, prepages = null, searchkey = null) {
		let roles = [{}]
		this.permission.getPriviliedgesType(pages, prepages, searchkey).subscribe(res => {
			console.log(res)
			res['result'].forEach(item => {
				if (item.type) {
					roles.push({ id: item._id, type: item.type,default:item.default, _childern: this.splitPriviliage(item['privilidge']), isSelected: false })
				}
			})
			this.dataSource=new MatTableDataSource(roles.slice(1,roles.length));
			console.log(this.dataSource)
			 this.dataSource.paginator = this.paginator;
			this.changeDetectorRefs.detectChanges();
		})

	}
	
	onSearchClear() {
	//	this.searchInput.nativeElement.value = ''
	//	this.applyFilter()
	}
	nextPage(event) {
		console.log(event)
		this.LoadPriviliages(event.pageIndex + 1, event.pageSize, null)
	}
	//check if dataSource has datasource
	checkedDataSource() {
		if (this.dataSource) {
			if (this.dataSource.data) {
				return true
			}
			else return false
		}
		else return false
	}
	checkedpermission(key)
	{
		if( localStorage.getItem('user'))
		{

			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge'])
			{
				if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Roles'])
				{
				console.log('user')
				if(key=='del')
				{
					console.log('del')
			  if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Roles']['Edit Roles'])
			  return true
				}
				else if(key=='update')
				{
					console.log('up')
					if( JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Roles']['Delete Roles'])
					return true
				}
				else if(key='add')
				{
					console.log('add')
            // if( JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Roles']['Add Roles'])
					return true
				}
			}
			}
		}

		return false
	}
	fetchUsers(){

	}
}
