// Angular
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
// Components
import { BaseComponent } from './views/theme/base/base.component';
import { ErrorPageComponent } from './views/theme/content/error-page/error-page.component';
// Auth
import { AuthGuard, } from './core/auth';
import {LoggedGuard} from './core/auth/_guards/logged.guard'
const routes: Routes = [
	{path: 'auth', loadChildren: () => import('./views/pages/auth/auth.module').then(m => m.AuthModule),canActivate:[LoggedGuard]}, 
	// {path :'',redirectTo:'default',pathMatch:'full'},
	// {path :'load',redirectTo:'default',pathMatch:'full'},
 	{
		path: '',
		 component: BaseComponent,
		 canActivate:[AuthGuard],
 		children: [
			
			{
				path: 'user-management',
				loadChildren: () => import('./views/pages/user-management/user-management.module').then(m => m.UserManagementModule)
			 },
			 {
				path: 'membership',
				loadChildren: () => import('./views/pages/memberships/membership.module').then(m => m.MembershipModule)
			 },
			 {
				path: 'billing',
				loadChildren: () => import('./views/pages/billing/billing.module').then(m => m.BillingModule)
 			},
			
			{path: 'error/:type', component: ErrorPageComponent},
 			{path: '**', redirectTo: 'membership', pathMatch: 'full'},
		],
	},
	{path: 'load', redirectTo: '/', pathMatch: 'full'},
	{path: '', redirectTo: '/', pathMatch: 'full'},
	{path: '**', redirectTo: 'error/403', pathMatch: 'full'},
];

@NgModule({
	imports: [
		RouterModule.forRoot(routes , {useHash:true})
	],
	exports: [RouterModule]
})
export class AppRoutingModule {
}
